html, body {
  overflow: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.clickable {
  cursor: pointer;
}

.unclickable {
  pointer-events: none;
}

.prevent-mobile-highlighting {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}



